$color-background: white;
$color-text: black;
$color-text-faded: #b0b0b0;
$color-code-block: #f6f6f6;
$color-code: #e2e6e8;
$color-link-light: #c4dbc6;
$color-link: #97bd99;
@media (prefers-color-scheme: dark) {
    $color-background: rgb(50, 50, 50);
    $color-text: rgb(225, 225, 225);
}

html {
    overflow-y: scroll;
    width: 640px;
    margin: auto;
}

body {
    font-family: monospace, monospace;
    // font-family: 'Lucida Grande', monospace;
    font-size: 18px;
    background: $color-background;
    color: $color-text;
    line-height: 1.3em;
}

hr {
    // margin: 12px 0px;
    border: 0;
    border-top: 1.9px solid $color-link;
}

header {
    font-size: 20px;
    line-height: 40px;
    & > nav {
        float: right;
    margin-left: 20px;
    font-size: 16px;
    color: $color-link;
    }
}

footer {
    font-size: 14px;
    color: $color-link;
    text-align: center;
    margin-top: 10px;
    margin-bottom: 30px;
}

main {
    margin: 30px 0px;
    &#homepage {
        text-align: center;
        img {
            display: block;
            margin-left: auto;
            margin-right: auto;
            // width: 25vw;
            width: 250px;
        }
        p.regular {
            font-size: 16px;
        }
    }
    &.regular {
        font-size: 16px;
    }
}

@media screen and (max-width: 700px) {
    html {
        width: 90vw;
    }
}

/* p, ul, ol {
    margin: 0px 0px 24px;
} */
ul.blog {
    list-style-type: none;
    margin: 0;
    padding: 0;
    li {
        margin: 30px 0;
        .date {
            float: right;
        }
        .excerpt {
            font-style: italic;
            color: $color-text-faded;
            padding-left: 25px;
        }
    }
}

.post_content {
    // font-family: 'Roboto', sans-serif;
    font-family: -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
    text-rendering: optimizelegibility;
    font-size: 1.125rem;
    line-height: 1.6em;
    h2, .large {
        font-size: 24px;
        color: $color-link;
    }
    p {
        margin: 25px 0;
    }
    .center {
        text-align: center;
    }
    .right {
        text-align: right;
    }
}

pre {
    background-color: $color-code-block;
    padding: 15px;
    border-left: 10px solid $color-code;
    overflow: auto;
    code {
        background-color: $color-code-block;
        padding: 0;
        // TODO change for dark mode
    }
    line-height: 1.2em;
}
code {
    background-color: $color-code;
    border-radius: 3px;
    padding: 1px 3px;
}

.potw {
    :nth-child(even) {
        pre {
            background-color: $color-code;
            code {
                background-color: $color-code;
            }
        }
    }
    pre {
        border-left: 0;
    }
    code {
        white-space : pre-wrap !important;
        overflow-wrap: break-word;
    }
    font-size: 16px;
}

img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 250px;
    max-width: 100%;
    height: auto;
    // width: 20vw;
    + em {
        text-align: center;
        display: block;
        margin-left: auto;
        margin-right: auto;
        max-width: 100vw;
        height: auto;
        width: 400px;
    }
}
.small {
    width: 150px;
}
.medium {
    width: 350px;
}
.large {
    width: 450px;
}
.full {
    width: 640px;
}

a {
    color: $color-link;
    text-decoration: none;
    &:hover, .current {
        font-weight: bold;
    }
}
.current {
    font-weight: bold;
}
.green {
    color: $color-link;
}

h1 {
    color: $color-text;
    font-size: 20px;
    line-height: 36px;
    margin: 24px 0;
    text-align: center;
}
h2 {
    color: $color-link;
    font-size: 20px;
    font-weight: normal;
    // line-height: 36px;
}
h3 {
    color: $color-text;
    font-size: 20px;
    font-weight: normal;
    text-decoration: underline;
    // line-height: 36px;
}

details.h2 {
    > summary {
        color: $color-link;
        font-size: 20px;
        cursor: pointer;
        margin: 1rem 0;
    }
    details {
        margin-left: 1rem;
        > summary {
            // font-size: 18px;
            text-transform: capitalize;
            cursor: pointer;
        }
    }    
}

.page_descriptor {
    font-style: italic; 
    color: $color-text-faded; 
    text-align: center;
}

.qotd {
    .date {
        margin-bottom: 0;
    }
    .author {
        text-align: right;
        margin-top: 0; 
        font-style: italic; 
        color: $color-text-faded;
    }
}